                <div class="txt">
                    将制作好的海报图，分享至朋友圈，保持一小时不删除，不屏蔽他人，将截图发送至<em>{{pageName === 'xc520'?'"学程职业在线"':'"弘成学习中心"'}}</em>公众号即可领现金红包！
