<script>
    import html2canvas from "html2canvas";
    import Exif from "exif-js";
    import $ from 'jquery'
    export default {
        data() {
            return {
                isShare: false,
                shareImgUrl: '',
                isShow: true,
                isIndex: false,
                curIndex: 0,
                imgArr: [],
                imgDef: [
                    require("../assets/imgs/photo-01.jpg"),
                    require("../assets/imgs/photo-02.jpg"),
                    require("../assets/imgs/photo-03.jpg"),
                    require("../assets/imgs/photo-04.jpg"),
                    require("../assets/imgs/photo-05.jpg"),
                    require("../assets/imgs/photo-06.jpg")
                ],
                imgUrl: "",
                initTouchX: 0,
                initTouchY: 0,
                changeTouchX: 0,
                changeTouchY: 0,
                reviewImgDom: "",
                lastTouchX: 0,
                lastTouchY: 0,
                previewImg: "",
                myImg: {
                    position: {
                        x: 0,
                        y: 0
                    },
                    scale: 1,
                    lastScale: 1
                },
                indexHide: true,
                isMessage: false,
                isUplGuide: true,
                showSetImgBtn : false,
                pageName : '',
                isUpload : false
            };
        },
        mounted() {

            let name = this.$route.name
            let xcImgArr = [
                require("../assets/imgs/xc-temp-01.png"),
                require("../assets/imgs/xc-temp-02.png"),
                require("../assets/imgs/xc-temp-03.png"),
                require("../assets/imgs/xc-temp-04.png"),
                require("../assets/imgs/xc-temp-05.png"),
                require("../assets/imgs/xc-temp-06.png")
            ]
            let hcImgArr = [
                require("../assets/imgs/hc-temp-01.png"),
                require("../assets/imgs/hc-temp-02.png"),
                require("../assets/imgs/hc-temp-03.png"),
                require("../assets/imgs/hc-temp-04.png"),
                require("../assets/imgs/hc-temp-05.png"),
                require("../assets/imgs/hc-temp-06.png")
            ]
            if (name === 'xc520') {
                this.imgArr = xcImgArr
            } else {
                this.imgArr = hcImgArr
            }
            this.pageName = name
            // 微信分享
            this.WXConfig.wxShowMenu(name);
            // 页面加载完隔1秒跳出信息框
            setTimeout(() => {

                document.addEventListener("WeixinJSBridgeReady", function () {
                    this.$refs.myAudio.play();
                }, false);


                this.isMessage = true;
            }, 1000);
            this.previewImg = document.querySelector("#preview-img");
            document.addEventListener("touchstart", function (event) {
                if (event.touches.length > 1) {
                    event.preventDefault();
                }
            });
            var lastTouchEnd = 0;
            document.addEventListener(
                "touchend",
                function (event) {
                    var now = new Date().getTime();
                    if (now - lastTouchEnd <= 300) {
                        event.preventDefault();
                    }
                    lastTouchEnd = now;
                },
                false
            );
            this.imgUrl = this.imgDef[0];
        },
        methods: {
            makeAgainFn() {
                this.isShare = false
                this.isIndex = true
                // this.imgUrl = ''
                this.myImg = Object.assign({},this.myImg,{
                    position: {
                        x: 0,
                        y: 0
                    },
                    scale: 1,
                    lastScale: 1
                })
            },
            getPhoto() {
                var imageInput = document.querySelector("#image-input");
                var that = this;
                imageInput.addEventListener("change", function (fileObj) {
                    var file = fileObj.target.files['0'];
                    //图片方向角
                    var Orientation = null;
                    if (file) {
                        //获取照片方向角属性，用户旋转控制
                        Exif.getData(file, function () {
                            Exif.getAllTags(this);
                            Orientation = Exif.getTag(this, 'Orientation');
                            //console.log(Orientation);
                        });
                        var oReader = new FileReader();
                        oReader.onload = function (e) {
                            var image = new Image();
                            image.src = e.target.result;
                            image.onload = function () {
                                var expectWidth = this.naturalWidth;
                                var expectHeight = this.naturalHeight;
                                if (this.naturalWidth > this.naturalHeight && this.naturalWidth > 800) {
                                    expectWidth = 800;
                                    expectHeight = expectWidth * this.naturalHeight / this.naturalWidth;
                                } else if (this.naturalHeight > this.naturalWidth && this.naturalHeight > 1200) {
                                    expectHeight = 1200;
                                    expectWidth = expectHeight * this.naturalWidth / this.naturalHeight;
                                }
                                var canvas = document.createElement("canvas");
                                var ctx = canvas.getContext("2d");
                                canvas.width = expectWidth;
                                canvas.height = expectHeight;
                                ctx.drawImage(this, 0, 0, expectWidth, expectHeight);
                                var base64 = null;
                                //修复ios
                                if (navigator.userAgent.match(/iphone/i)) {
                                    //console.log('iphone');
                                    //如果方向角不为1，都需要进行旋转
                                    if (Orientation != "" && Orientation != 1) {
                                        switch (Orientation) {
                                            case 6://需要顺时针（向左）90度旋转
                                                that.rotateImg(this, 'left', canvas);
                                                break;
                                            case 8://需要逆时针（向右）90度旋转
                                                that.rotateImg(this, 'right', canvas);
                                                break;
                                            case 3://需要180度旋转
                                                that.rotateImg(this, 'right', canvas);//转两次
                                                that.rotateImg(this, 'right', canvas);
                                                break;
                                        }
                                    }
                                    base64 = canvas.toDataURL("image/jpeg", 0.5);
                                } else {
                                    if (Orientation != "" && Orientation != 1) {
                                        switch (Orientation) {
                                            case 6://需要顺时针（向左）90度旋转
                                                that.rotateImg(this, 'left', canvas);
                                                break;
                                            case 8://需要逆时针（向右）90度旋转
                                                that.rotateImg(this, 'right', canvas);
                                                break;
                                            case 3://需要180度旋转
                                                that.rotateImg(this, 'right', canvas);//转两次
                                                that.rotateImg(this, 'right', canvas);
                                                break;
                                        }
                                    }

                                    base64 = canvas.toDataURL("image/jpeg", 0.5);
                                }
                                // that.isDefault = false
                                that.imgUrl = base64;
                                that.isUpload = true
                                // console.log(this.isDefault)
                                $(".btns").addClass("cav");

                            };
                        };
                        oReader.readAsDataURL(file);
                    }
                });
            },
            //对图片旋转处理
            rotateImg(img, direction, canvas) {
                var min_step = 0;
                var max_step = 3;
                if (img == null) return;
                var height = img.height;
                var width = img.width;
                var step = 2;
                if (step == null) {
                    step = min_step;
                }
                if (direction == 'right') {
                    step++;
                    //旋转到原位置，即超过最大值
                    step > max_step && (step = min_step);
                } else {
                    step--;
                    step < min_step && (step = max_step);
                }
                //旋转角度以弧度值为参数
                var degree = step * 90 * Math.PI / 180;
                var ctx = canvas.getContext('2d');
                switch (step) {
                    case 0:
                        canvas.width = width;
                        canvas.height = height;
                        ctx.drawImage(img, 0, 0);
                        break;
                    case 1:
                        canvas.width = height;
                        canvas.height = width;
                        ctx.rotate(degree);
                        ctx.drawImage(img, 0, -height);
                        break;
                    case 2:
                        canvas.width = width;
                        canvas.height = height;
                        ctx.rotate(degree);
                        ctx.drawImage(img, -width, -height);
                        break;
                    case 3:
                        canvas.width = height;
                        canvas.height = width;
                        ctx.rotate(degree);
                        ctx.drawImage(img, -width, 0);
                        break;
                }
            },


            changeIndex(index) {
                this.curIndex = index;
                if (!this.isUpload) {
                    this.imgUrl = this.imgDef[this.curIndex];
                }
            },
            getInitPosition(e) {
                event.preventDefault();
                if (this.imgUrl) {
                    var length = e.touches.length;
                    if (length > 1) {
                        let pointOne = e.touches[0];
                        let pointTwo = e.touches[1];
                        this.initTouchX = pointOne.clientX - pointTwo.clientX;
                        this.initTouchY = pointOne.clientY - pointTwo.clientY;
                    } else {
                        var touches = e.touches[0];
                        this.initTouchX = touches.clientX;
                        this.initTouchY = touches.clientY;
                    }
                }
            },
            getMovePosition(e) {
                event.preventDefault();
                if (this.imgUrl) {
                    var length = e.touches.length;
                    if (length > 1) {
                        let pointOne = e.touches[0];
                        let pointTwo = e.touches[1];
                        this.changeTouchX = pointOne.clientX - pointTwo.clientX;
                        this.changeTouchY = pointOne.clientY - pointTwo.clientY;
                        var scale =
                            this.changeTouchX - this.initTouchX >
                            this.changeTouchY - this.initTouchY
                                ? this.changeTouchX / this.initTouchX
                                : this.changeTouchY / this.initTouchY;
                        scale *= this.myImg.lastScale;
                        this.myImg.scale = scale > 3 ? 3 : scale < 0.5 ? 0.5 : scale;
                    } else {
                        var touches = e.touches[0];
                        this.changeTouchX = touches.clientX - this.initTouchX;
                        this.changeTouchY = touches.clientY - this.initTouchY;
                        this.myImg.position.x =
                            this.lastTouchX + this.changeTouchX / this.myImg.scale;
                        this.myImg.position.y =
                            this.lastTouchY + this.changeTouchY / this.myImg.scale;
                    }
                }
            },
            getLeavePosition(e) {
                this.myImg.lastScale = this.myImg.scale;
                if (e.touches.length > 0) {
                    var touches = e.touches[0];
                    this.initTouchX = touches.clientX;
                    this.initTouchY = touches.clientY;
                }
                this.lastTouchX = this.myImg.position.x;
                this.lastTouchY = this.myImg.position.y;
            },
            createPhoto() {
                if (this.imgUrl) {
                    let that = this;
                    let photoBox = document.querySelector(".photo-box");
                    let scale = window.devicePixelRatio;
                    let width = photoBox.offsetWidth;
                    let height = photoBox.offsetHeight;
                    window.scrollTo(0,0)
                    html2canvas(photoBox, {
                        width : width,
                        height : height,
                        scale: scale,
                        useCORS: true
                    }).then(function (canvas) {
                        var dataUrl = canvas.toDataURL("image/jpg");
                        // console.log(dataUrl);
                        localStorage.imgData = dataUrl;
                        that.shareImgUrl = localStorage['imgData']
                        setTimeout(function () {
                            that.isShow = false
                        }, 2000)
                        that.isShare = true
                    }).catch(err => {
                       
                    })
                } else {
                    alert("请上传图片");
                }
            },
            thanksFn() {
                // this.$refs.gdpage.style.display = "none";
                // this.$refs.makePhoto.style.display = "block";
                this.isIndex = true
                // 上传照片页显示时，2秒后指引消失
                setTimeout(() => {
                    this.isUplGuide = false
                }, 2000);
            },
        },
        destroyed() {
            let _this = this;
            clearInterval(_this.timer);
        }
    };
</script>

