import wx from 'weixin-js-sdk'
import axios from 'axios'
export default {
  wxShowMenu: function (name) {
    axios.post('https://uec.chinaedu.net/wx/api/app_server/encryption',{
      url: window.location.href.split('#')[0],
      appid : name === 'hc520'?'wx5766328e8cf6a21a':'wxcee66ea3f6ea549b'
    }).then(function (res) {
      console.log(res)
      var getMsg = res.data.data;
      var imgUrl = 'https://xxzx.chinaedu.net/wx/img/1589788981723.jpg';
      var shareLink = window.location.href
      var shareTitle = '爱就要show出来';
      var shareDesc = '晒出与你钟爱的TA的合影，赢现金红包！';

      //console.log(getMsg);

      wx.config({
        debug: false, // 生产环境需要关闭debug模式
        appId: getMsg.appid, // appId通过微信服务号后台查看
        timestamp: getMsg.timestamp, // 生成签名的时间戳
        nonceStr: getMsg.nonceStr, // 生成签名的随机字符串
        signature: getMsg.signature, // 签名
        jsApiList: [ // 需要调用的JS接口列表
          'onMenuShareTimeline', // 分享给好友
          'onMenuShareAppMessage', // 分享到朋友圈
          'translateVoice',
        ]
      });
      wx.ready(function () {
        wx.onMenuShareTimeline({
          title: shareTitle, // 分享标题
          desc: shareDesc, // 分享描述
          link: shareLink, // 分享链接
          imgUrl: imgUrl // 分享图标
        });
        // 分享给朋友
        wx.onMenuShareAppMessage({
          title: shareTitle, // 分享标题
          desc: shareDesc, // 分享描述
          link: shareLink, // 分享链接
          imgUrl: imgUrl // 分享图标
        });
      });
      wx.error(function (res) {
        // console.log(res)
      });
    }).catch(function (err) {
      // console.log('页面错误：', err)
    });
  }
}
